import React, { useState, useRef, useEffect } from "react";
import { IoIosPlay, IoIosPause } from "react-icons/io";

// style //
import styled from "styled-components";
import { fontSize, primary } from "./style/fonts";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 8%;
`;

const StyledInput = styled.input`
  display: none;
`;

const StyledLabel = styled.label`
  margin: 16px 0px;
  border: 1px solid black;
  padding: 6px 12px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  background-color: white;
  min-width: 100px;
  transition: color 0.25s, background-color 0.25s;

  &:hover {
    color: white;
    background-color: black;
  }
`;

const VisualizerControls = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
`;

const UploadAndTitle = styled.div`
  display: flex;
  min-width: 0px;
`;

const FileNameContainer = styled.div`
  overflow: hidden;
  display: flex;
  align-items: center;
  min-width: 0px;
`;

const FileName = styled.div`
  font-size: ${fontSize.sm};
  line-height: 20px;
  margin-left: 16px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
`;

const ButtonText = styled.div`
  font-size: ${fontSize.sm};
  line-height: 20px;
`;

const StyledPlayIcon = styled(IoIosPlay)`
  color: black;
  cursor: pointer;
  font-size: 28px;
  margin-right: 8px;

  &:hover {
    color: ${primary};
  }
`;

const StyledPauseIcon = styled(IoIosPause)`
  color: black;
  cursor: pointer;
  font-size: 28px;
  margin-right: 8px;

  &:hover {
    color: ${primary};
  }
`;

const RATIO = 2.266;

const getWidth = () => {
  const maxCanvasWidth = 1250;

  if (window.innerWidth > maxCanvasWidth) return maxCanvasWidth;

  if (window.innerWidth < maxCanvasWidth) return window.innerWidth - 16;
};

const calcBarHeight = (barHeight, height) => {
  const barHeightPercent = barHeight / 255;
  return Math.floor(barHeightPercent * height);
};

let isSafari;

if (typeof window !== `undefined`) {
  // eslint-disable-next-line no-native-reassign
  AudioContext = window.AudioContext || window.webkitAudioContext;
  isSafari = !!window.webkitAudioContext;
}

function getDft(dft) {
  switch(dft){
    case 0:
      return 32;
    case 25:
      return 128;
    case 50:
      return 256;
    case 75:
      return 2048;
    case 100:
      return 8192;
    default:
      return 2048;
  }
}

function getSpeed(speed) {
  switch(speed){
    case 0:
      return 0.25;
    case 25:
      return 0.5;
    case 50:
      return 1;
    case 75:
      return 1.5;
    case 100:
      return 2;
    default:
      return 1;
  }
}

function convertAnimationSpeed(speed) {
  switch(speed){
    case 0:
      return 0.4;
    case 50:
      return 0.85;
    case 100:
      return 0.99;
    default:
      return 0.85;
  }
}

function getShadesOfColor(color) {
  let res = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(color);
  let r = parseInt(res[1], 16);
  let g = parseInt(res[2], 16);
  let b = parseInt(res[3], 16);
  let result = [];
  for(let i = 0; i < 4; i++)
  {
      r += 33;
      g += 33;
      b += 33;
      result.push(rgbToHex(r,g,b));
  }
  return result;
}

function componentToHex(c) {
  let hex = c.toString(16);
  return hex.length == 1 ? "0" + hex : hex;
}

function rgbToHex(r, g, b) {
  return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
}

const Visualizer = ({options}) => {
  const color = options.color;
  const speed = options.speed;
  const dft = getDft(options.dft);
  const useGradient = options.gradient;
  const animationSpeed = convertAnimationSpeed(options.width);
  const [stateWidth, setWidth] = useState(getWidth());
  const [fileName, setFileName] = useState("");
  const [showIcon, setShowIcon] = useState(null);

  const width = useRef(getWidth());
  const audioCtx = useRef(new AudioContext());
  const analyser = useRef(audioCtx.current.createAnalyser());
  const canvas = useRef(null);
  const input = useRef(null);
  const canvasCtx = useRef();
  const dataArray = useRef();
  const source = useRef();

  useEffect(() => {
    canvasCtx.current = canvas.current.getContext("2d");
    let curr = canvasCtx.current;
    const handleResize = () => {
      setWidth(getWidth());

      width.current = getWidth();
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
      curr.close();
    };
  }, []);

  const handlePlayClick = () => {
    if (audioCtx.current.state === "suspended") {
      audioCtx.current.resume().then(function() {
        setShowIcon("pause");
      });
    }
  };

  const handlePauseClick = () => {
    if (audioCtx.current.state === "running") {
      audioCtx.current.suspend().then(function() {
        setShowIcon("play");
      });
    }
  };

  const handleFiles = () => {
    const reader = new FileReader();
    source.current = audioCtx.current.createBufferSource();

    if (input.current.files.length !== 1) {
      alert("You may only select a single file!");
      return;
    }

    setFileName(input.current.files[0].name);
    reader.readAsArrayBuffer(input.current.files[0]);

    reader.onloadend = e => {
      audioCtx.current.decodeAudioData(e.target.result).then(decodedData => {
        source.current.buffer = decodedData;
        source.current.connect(analyser.current);
        analyser.current.connect(audioCtx.current.destination);

        setUp();
      });
    };
  };

  const setUp = () => {
    // audio config
    analyser.current.minDecibels = -90;
    analyser.current.maxDecibels = -10;
    analyser.current.smoothingTimeConstant = animationSpeed;
    analyser.current.fftSize = dft;
    // 8-bit unsigned integers
    dataArray.current = new Uint8Array(analyser.current.frequencyBinCount);

    //begin
    setShowIcon(isSafari ? "play" : "pause");
    source.current.start(0);
    source.current.playbackRate.value=getSpeed(speed);

    setInterval(() => {
      requestAnimationFrame(draw);
    }, 1000 / 40);
  };

  // use callback?
  const draw = () => {
    const WIDTH = width.current;
    const HEIGHT = width.current / RATIO;
    const bufferLength = analyser.current.frequencyBinCount;
    analyser.current.getByteFrequencyData(dataArray.current);
    canvasCtx.current.clearRect(0, 0, WIDTH, HEIGHT);
    const barWidth = (WIDTH - bufferLength) / bufferLength;
    let barHeight;
    let x = 0;
    let gradient;
    if(useGradient) {
      gradient = canvasCtx.current.createLinearGradient(0, 0, 200, 0);
      let shades = getShadesOfColor(color)
      console.log(shades);
      gradient.addColorStop(0, "#7cf987");
      gradient.addColorStop(.7, "#6194cf");
      gradient.addColorStop(1, "#0000ff");
    }

    for (let i = 0; i < bufferLength; i++) {
      barHeight = calcBarHeight(dataArray.current[i], HEIGHT);
      if(useGradient)
        canvasCtx.current.fillStyle = gradient;
      else canvasCtx.current.fillStyle = color;
      canvasCtx.current.fillRect(x, HEIGHT - barHeight, barWidth, barHeight);

      x += barWidth + 1;
    }
  };

  const showButton = () => {
    if (audioCtx.current.state === "running")
      return <StyledPauseIcon onClick={handlePauseClick} />;

    return <StyledPlayIcon onClick={handlePlayClick} />;
  };

  return (
    <div>
      <Container>
      <canvas
        ref={canvas}
        height={stateWidth / RATIO}
        width={stateWidth}
      />
      <VisualizerControls>
        <UploadAndTitle>
          <StyledLabel for="file-upload">
            <ButtonText>Select File</ButtonText>
          </StyledLabel>
          <StyledInput
            type="file"
            id="file-upload"
            onChange={handleFiles}
            ref={input}
          />
          <FileNameContainer>
            <FileName>{fileName}</FileName>
          </FileNameContainer>
        </UploadAndTitle>
        <div>{showIcon !== null && showButton()}</div>
      </VisualizerControls>
    </Container>
    </div>
  );
};

export default Visualizer;
