import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import EqualizerIcon from '@material-ui/icons/Equalizer';
import BurstModeIcon from '@material-ui/icons/BurstMode';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Slider from '@material-ui/core/Slider';
import ColorPicker from 'material-ui-color-picker';
import Box from '@material-ui/core/Box';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import Select from '@material-ui/core/Select';
import Visualizer from './Visualizer';
import MenuItem from '@material-ui/core/MenuItem';
import Switch from '@material-ui/core/Switch';


const drawerWidth = 240;
const primary = "#650d88";
const useStyles = makeStyles((theme) => ({
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
  root: {
    display: 'flex',
  },
  appBar: {
    backgroundColor: primary,
    color: "#eda140",
    textShadow: "-0.3px -0.3px 0 #f1b568, 0.3px -0.3px 0 #f1b568, -0.3px 0.3px 0 #f1b568, 0.3px 0.3px 0 #f1b568",
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
  logo: {
    bottom: 1,
  },
  panel: {
    display: "flow-root",
  },
  bar: {
    width: "100%",
  },
  boxes: {
    marginTop: "10%",
  }
}));



function SideBar() {
  const classes = useStyles();
  const theme = useTheme();

  const [open, setOpen] = React.useState(false);
  
  const [expanded, setExpanded] = React.useState(false);

  const [widthvalue, setWidthValue] = React.useState(50);
  const [colorvalue, setColorValue] = React.useState("#f1b568");

  const [volumevalue, setVolumeValue] = React.useState(50);
  const [speedvalue, setSpeedValue] = React.useState(50);
  const [dftvalue, setDftValue] = React.useState(50);

  const [gradient, setGradient] = React.useState(false);

  const handleChangeVolume = (event, newValue) => {
    setVolumeValue(newValue);
  };

  const handleChangeWidth = (event, newValue) => {
    setWidthValue(newValue);
  };

  const handleChangeSpeed = (event, newValue) => {
    setSpeedValue(newValue);
  };

  const handleChangeDft = (event, newValue) => {
    setDftValue(newValue);
  };

  const handleChangeColor = (color) => {
    setColorValue(color);
  };

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  const handleGradient = () => {
    setGradient(!gradient);
  }


  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  function valuetext(value) {
    return `${value}x`;
  }

  const speed = [   
    {
      value: 0,
      label: '0.2x',
    },
    {
      value: 25,
      label: '0.5x',
    },
    {
      value: 50,
      label: '1x',
    },
    {
      value: 75,
      label: '1.5x',
    },
    {
      value: 100,
      label: '2x',
    },
  ];

  const dft = [
    {
      value: 0,
      label: 'basic',
    },
    {
      value: 25,
      label: 'low',
    },
    {
      value: 50,
      label: 'med',
    },
    {
      value: 75,
      label: 'smooth',
    },
    {
      value: 100,
      label: 'high',
    },
  ];
  
  const animation = [
    {
      value: 0,
      label: 'fast',
    },
    {
      value: 50,
      label: 'normal',
    },
    {
      value: 100,
      label: 'smooth',
    },
  ];

  function speedLabelFormat(value) {
    return speed.findIndex((mark) => mark.value === value) + 1;
  }

  function dftLabelFormat(value) {
    return dft.findIndex((mark) => mark.value === value) + 1;
  }

  function animationLabelFormat(value) {
    return animation.findIndex((mark) => mark.value === value) + 1;
  }
  
  

  return (
    <ClickAwayListener onClickAway={handleDrawerClose}>
      <div className={classes.root}>
        <CssBaseline />
        <AppBar
          position="fixed"
          className={clsx(classes.appBar, {
            [classes.appBarShift]: open,
          })}
        >
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              edge="start"
              className={clsx(classes.menuButton, open && classes.hide)}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" noWrap>
              AudioViz
            </Typography>
          </Toolbar>
        </AppBar>
        <Drawer
          className={classes.drawer}
          variant="persistent"
          anchor="left"
          open={open}
          classes={{
            paper: classes.drawerPaper,
          }}
        >
          <List>
              <ListItem button key={"Animation settings"}>
                <ExpansionPanel expanded={expanded === 'panel1'} onChange={handleChange('panel1')}>
                  <ExpansionPanelSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel4bh-content"
                    id="panel4bh-header"
                  >
                    <ListItemIcon> <BurstModeIcon /> </ListItemIcon>
                <ListItemText primary={"Animation settings"} />
                  </ExpansionPanelSummary>
                  <ExpansionPanelDetails className={classes.panel}>
                    <Box>
                      <Typography id="continuous-slider" className={classes.bar} gutterBottom >
                        Animation Type
                      </Typography>
                      <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value="Bars"
                        className={classes.bar}
                        disabled
                      >
                        <MenuItem value={"Bars"}>Bars</MenuItem>
                      </Select>
                    </Box>
                    <Box className={classes.boxes}>
                      <Typography id="continuous-slider" gutterBottom>
                        Speed
                      </Typography>
                      <Slider 
                        defaultValue={50} 
                        value={widthvalue} 
                        onChange={handleChangeWidth} 
                        valueLabelFormat={animationLabelFormat}
                        getAriaValueText={valuetext}
                        aria-labelledby="discrete-slider-restrict"
                        step={null}
                        valueLabelDisplay="auto"
                        marks={animation}
                      />
                    </Box>
                    <Box className={classes.boxes}> 
                    <Typography id="continuous-slider" gutterBottom>
                      Color
                    </Typography>
                      <ColorPicker
                        name='color'
                        value={colorvalue || "#f1b568"} 
                        onChange={handleChangeColor}
                      />
                    </Box>
                    <Box className={classes.boxes}>
                      <Typography id="continuous-slider" gutterBottom>
                        Use Gradient
                      </Typography>
                      <Switch
                        checked={gradient}
                        onChange={handleGradient}
                        name="Use Gradient"
                        inputProps={{ 'aria-label': 'secondary checkbox' }}
                      />
                    </Box>
                  </ExpansionPanelDetails>
                </ExpansionPanel>
              </ListItem>
              <ListItem button key={"Sound settings"}>
              <ExpansionPanel expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
                  <ExpansionPanelSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel4bh-content"
                    id="panel4bh-header"
                  >
                      <ListItemIcon> <EqualizerIcon /> </ListItemIcon>
                <ListItemText primary={"Sound settings"} />
                  </ExpansionPanelSummary>
                  <ExpansionPanelDetails className={classes.panel}>
                    <Box>
                      <Typography id="continuous-slider" gutterBottom>
                        Volume
                      </Typography>
                      <Slider value={volumevalue} onChange={handleChangeVolume} aria-labelledby="continuous-slider" />
                    </Box>
                    <Box>
                      <Typography id="continuous-slider" gutterBottom>
                        Speed 
                      </Typography>
                      <Slider
                        defaultValue={85}
                        valueLabelFormat={speedLabelFormat}
                        getAriaValueText={valuetext}
                        aria-labelledby="discrete-slider-restrict"
                        step={null}
                        valueLabelDisplay="auto"
                        onChange={handleChangeSpeed}
                        marks={speed}
                      />
                    </Box>
                    <Box>
                      <Typography id="continuous-slider" gutterBottom>
                        DFT 
                      </Typography>
                      <Slider
                        defaultValue={50}
                        valueLabelFormat={dftLabelFormat}
                        getAriaValueText={valuetext}
                        aria-labelledby="discrete-slider-restrict"
                        step={null}
                        valueLabelDisplay="auto"
                        onChange={handleChangeDft}
                        marks={dft}
                      />
                    </Box>
                  </ExpansionPanelDetails>
                </ExpansionPanel>
              </ListItem>
          </List>
          <div className={classes.logo}>
        <IconButton onClick={handleDrawerClose}>
                <img src="/logo.png" alt="logo"></img>
                {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
              </IconButton>
          </div>
        </Drawer>
        <Visualizer options={{width: widthvalue, color: colorvalue, volume: volumevalue, speed: speedvalue, dft: dftvalue, gradient: gradient}}/>
      </div>
    </ClickAwayListener>
  );
}

export default SideBar